			WebFont license rules. All other parties are strictly restricted
			from using the WebFonts(s) listed without a purchased license.
			All details above must always remain unaltered and visible in your CSS.
*/

@font-face {
  font-family: 'Gilmer';
  src: url('./assets/fonts/Glimer/gilmer-regular.eot');
  src:
          url('./assets/fonts/Glimer/gilmer-regular.eot?#iefix') format('embedded-opentype'),
          url('./assets/fonts/Glimer/gilmer-regular.woff2') format('woff2'),
          url('./assets/fonts/Glimer/gilmer-regular.woff') format('woff'),
          url('./assets/fonts/Glimer/gilmer-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

